form.auth {
    border: 3px solid #f1f1f1;
    max-width: 400px;
    width: 90%;
    margin: 100px auto;
}

form.auth input[type=text], input[type=password] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
}

form.auth button {
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
}

form.auth button:hover {
    opacity: 0.8;
}

form.auth .container {
    padding: 16px;
}